(function(win) {
    class Observer {
        public static getInstance() {
            if (!this._instance) {
                this._instance = new this();
            }
            return this._instance;
        }
        private static _instance: Observer;
        private message: {[key: string]: Function[]} = {};
        private constructor() {

        }
        public regist(type: string, fn: Function) {
            if (!this.message[type]) {
                this.message[type] = [];
            }
            this.message[type].push(fn);
        }
        public fire(type: string, data: any) {
            if (!this.message[type]) { return; }
            this.message[type].forEach((fn) => fn(data));
        }
        public remove(type: string, fn: Function) {
            if (!this.message[type]) { return; }
            for (let i = 0, len = this.message[type].length; i < len; i++) {
                if (this.message[type][i] === fn) {
                    this.message[type].splice(i, 1);
                }
            }
        }
    }

    // @ts-ignore
    win.Observer = Observer;
})(window);
