














































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import topic from './topic.vue';
import answer_type1 from './answer_type1.vue';
import answer_type2 from './answer_type2.vue';
import singleRanking from '@/components/topic/ranking/singleRanking.vue';
import topicRanking from '@/components/topic/ranking/index.vue';

import { studentResultType3 } from '@/mock/mockJson';


const topic_type = 7002;

@Component({
    components: {
        topicInfo: topic,
        answer_type1,
        answer_type2,
        singleRanking,
        topicRanking,
    }
})
export default class App extends Vue {
    @Prop(Object) readonly params: any;
    @Prop(Array) readonly rankingData: any;
    @Prop(Number) readonly pageIndex: number;
    @Prop(Boolean) readonly showSendRanking: boolean;
    
    private topicList: any[] = [];
    private activeIndex: number = 0;
    private timeDownNum: number = 0; // 倒计时总时间
    private restTime: number = 0;// 倒计时剩余时间
    // private timer: any = null;
    private closeDisplay: boolean = true;
    private timeDownTime: any

    private classroomType: number = window.GlobalData? window.GlobalData.roomType : 2;// 教室类型：1、ONE；2、MANY；3、LIVE
    private showSingleRanking: boolean = false;
    private showRanking: boolean = false;

    private rightUsersData: any[] = [];
    private rightUserTime: any;
    private explainDisable: boolean = false;


    get currentComponent() {
        return this.currentTopic.type === 1 ? 'answer_type1' : 'answer_type2'
    }

    get currentTopic() {
        return this.topicList[this.activeIndex]? this.topicList[this.activeIndex] : {};
    }
    get showStudent() {
        //@ts-ignore;
        return  !!(this.classroomType !== 3);
    }
    get answer() {
        this.currentTopic.rightAnswer;
        if (this.currentTopic && this.currentTopic.type === 5) {
            if(this.currentTopic.rightAnswer === "A") {
                return '正确'
            } else {
                return '错误'
            }
        }
        return this.currentTopic.rightAnswer;
    }

    get timeDown() {
        const minutes = Math.floor(this.restTime / 60);
        const seconds = this.restTime % 60;
        if(minutes) {
            return `${minutes}分${seconds}秒`;
        } else {
            return `${seconds}秒`;
        }
    }

    changeTopic(item: any,index: number) {
        if(item.status !== 'init') { return };
        this.activeIndex = index;
        this.topicList = this.topicList.map(_item => {
            if(_item.id  === item.id) {
                _item.status = 'active'
                // this.studentResult = _item.studentResult ? _item.studentResult : [];
            } else {
                _item.status = 'init'
            }
            return _item;
        })
    }

    sendQuestion() {
        this.sendQuestionMsg();
    }

    startTimeDown() {
        this.timeDownNum = this.currentTopic.timeDown;
        // this.restTime = this.currentTopic.timeDown;
    }

    // 该题答题时间结束后的操作
    timeOver(showSingleRanking: boolean) {
			
			// 题目切换功能重新开启
			this.topicList = this.topicList.map(item => {
					if(item.status === 'disabled') {
							item.status = 'init';
					}
					return item;
			}); 
			this.closeDisplay = true;   
    	if (!showSingleRanking) return;
			this.rightUserTime && clearTimeout(this.rightUserTime);
      if(this.currentTopic.type === 1) {
        const studentResult = this.currentTopic.studentResult.find((item: any) => item.option === this.currentTopic.rightAnswerIndex[0])
        studentResult && (
          this.rightUsersData = studentResult.users
        )
        
      } else {
        const studentResult = this.currentTopic.studentResult.find((item: any) => item.text === '正确')
        studentResult && (
          this.rightUsersData = studentResult.users
        )
			}
			if (!this.rightUsersData || this.rightUsersData.length === 0) return;
      this.showSingleRanking = true;
			this.rightUserTime = setTimeout(() => {
					this.showSingleRanking = false;
			}, 3000);
    }
    // 发送倒计时结束指令
    sendTimeOverMsg() {
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        const elementId = topicModule.id;
        const msg = {
            moduleType: 1,
            cmd: 7055,
            type: 8,
            id: elementId,
            msg: {
                id: this.currentTopic.id,
            }
        }
        // @ts-ignore
        window.PageMgr.sendMsg(JSON.stringify(msg));
    }
    close() {
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        // topicModule.props.questionList = this.topicList;
        this.sendCloseMsg();
    }

    receiveQuestionMsg(msgJson: any) {
        const { msg, time } =  msgJson
        console.log('receiveQuestionMsg -->');
        const topicId = msg.id;

        const topic = this.topicList.find(item => item.id === topicId);
        const timeDown = topic.timeDown
        this.topicList = this.topicList.map((item: any) => {
            if(item.id === topicId) {
                item.answerStatus = 'answering';
            }
            return item;
        })
        const cb = (timeStamp: any) => {
            const countDown = parseInt(Number(timeStamp - time) / 1000 + '');
            // if ()
            if (countDown >= timeDown) return console.log('>>>  发送问题指令 当前问题已结束');
            this.closeDisplay = false;
            this.topicList = this.topicList.map(item => {
                if(item.status === 'init') {
                    item.status = 'disabled';
                }
                return item;
            })
            // this.initStudentResult();
            this.startTimeDown();
        }
        // const topicIndex = this.topicList.findIndex(item => item.id === topicId);
        // this.changeTopic(topic, topicIndex);
        // @ts-ignore
         window.ucPlugin && window.ucPlugin.getServerTime().then((res: any) => {
            console.log('>>> 获取服务器当前时间', res);
            const timeStamp = new Date(res.time).valueOf()
            cb(timeStamp)
         })
        
    }

    sendQuestionMsg() {
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        const elementId = topicModule.id;
        const msg = {
            id: elementId,
            moduleType: 1,
            cmd: 7011,
            type: 5,
            msg: {
                id: this.currentTopic.id,
                askTime: this.currentTopic.timeDown,
            },
            time: new Date().valueOf(),
        };
        const timeDownMsg = {
            id: elementId,
            moduleType: 1,
            type: 7,
            cmd: 7031,
            msg: {
                id: this.currentTopic.id,
                askTime: this.currentTopic.timeDown, 
            },
            time: new Date().valueOf(),
        }
        
        // @ts-ignore
        window.ucPlugin && window.ucPlugin.getServerTime().then((res: any) => {
            console.log('>>> 获取服务器当前时间', res);
            const timeStamp = new Date(res.time).valueOf()
            timeDownMsg.time = timeStamp;
            msg.time = timeStamp;
            // @ts-ignore
            window.PageMgr.sendMsg(JSON.stringify(timeDownMsg));
            // @ts-ignore
            window.PageMgr.sendMsg(JSON.stringify(msg));
        })
    }
    handleTopicTab(item: any, index: number) {
        console.log('>>>> changeTopic', item)
        if(item.status !== 'init') { return };
        this.explainDisable = false;
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        const elementId = topicModule.id;
        const msg = {
            id: elementId,
            moduleType: 1,
            cmd: 7044,
            type: 7,
            msg: {
                id: item.id,
                index, 
            },
        };
        // @ts-ignore
        window.PageMgr.sendMsg(JSON.stringify(msg));
    }

    sendCloseMsg() {
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        const elementId = topicModule.id;
        const msg = {
            id: elementId,
            moduleType: 1,
            cmd: 7002,
            type: 2,
        };
        // @ts-ignore
        window.PageMgr.sendMsg(JSON.stringify(msg));
    }
    // 发送排名
    sendRankingMsg() {
    // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) return;
        const elementId = topicModule.id;
        const msg = {
            moduleType: 1,
            cmd: 7066,
            type: 2,
            id: elementId,
        }
        // @ts-ignore
        window.PageMgr.sendMsg(JSON.stringify(msg));
    }
    // 习题讲解
    sendSetonMsg() {
        // @ts-ignore
        const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
        if(!topicModule) {
          return;
        }
        const elementId = topicModule.id;
        const msg = {
            id: elementId,
            moduleType: 1,
            cmd: 7011,
            type: 5,
            timeOver: true,
            msg: {
                id: this.currentTopic.id,
                askTime: this.currentTopic.timeDown,
            },
        };
        this.explainDisable = true;
        // @ts-ignore
        window.PageMgr.sendMsg(JSON.stringify(msg));
    }
    mounted() {
        // 处理数据
        this.topicList = this.params.topicList.map((item: any) => Object.assign({}, item, {
            status: 'init',
            // 判断是否已经答题结束 结束了显示学生答题结果
            answerStatus:item.timeOver ? 'answering': 'default',
        }));
        this.changeTopic(this.topicList[0], 0);

        // 监控事件
        this.$root.$on('event_questionMsg', (msg: any) => {
            this.receiveQuestionMsg(msg);
        })
        this.$root.$on('event_tabChange', (msgJson: any) => {
            const { id, index } = msgJson.msg;
            this.topicList.forEach((item: any, index: number) => {
                if(item.id === id) {
                    // this.activeIndex = index;
                    this.changeTopic(item, index);
                }
            })
        })
        
        this.$root.$on('event_studentResultSingle', (receivemsg: any) => {
            console.log('单个学生答题');
            const { msg,  user} = receivemsg;
            // 答题后改变右侧展示栏状态（此处处理刷新时 发送题指令只有一个其他已经答题的answerStatus 状态不对问题）
            this.topicList = this.topicList.map((item: any) => {
                if (item.id === msg.id) {
                    item.answerStatus = 'answering';
                }
                return item;
            })
        })

        this.$root.$on('event_countDown', (receivemsg: any) => {
            const { msg, time } = receivemsg
            const { id,  askTime}  = msg;
            const cb = (timeStamp: any) =>  {
                if (id !== this.currentTopic.id) return;
                const currentTime = timeStamp;
                const countDown = parseInt(Number(currentTime - time) / 1000 + '')  ;
                if (countDown >= askTime) {
                    this.restTime = 0;
                    this.timeOver(false);
                    this.sendTimeOverMsg();
                } else {
                    this.restTime = askTime - countDown;
                    this.timeDownTime && (clearInterval(this.timeDownTime))
                    this.timeDownTime = setInterval(() => {
                        // console.log('>>>  倒计时 teacher')
                        if(this.restTime <= 0) {
                            this.restTime = 0
                            this.timeOver(true);
                            this.sendTimeOverMsg();
                            clearInterval(this.timeDownTime)
                        } else {
                            this.restTime--
                        }
                    }, 1000)
                }
            }
            // @ts-ignore
            window.ucPlugin && window.ucPlugin.getServerTime().then((res: any) => {
                console.log('>>> 获取服务器当前时间', res);
                const timeStamp = new Date(res.time).valueOf()
                cb(timeStamp);
            })
        });
        this.$root.$on('event_showRanking', () => { 
            this.showRanking = true;
            setTimeout(() => {
                // @ts-ignore
                // 防止动画时间过长导致互动答题界面点击无效
                window.Lottie && Lottie.hiddenContainer();
                this.showRanking = false;
            }, 5000);
        })
        console.log('>>>>>>>>>>> teacher mounted -->');
        this.$root.$emit('event_teacherPageIsLoad');
    }
    @Watch('params', {deep: true})
    paramsChange(val: any, oldval: any) {
        console.log('>>>> 老师端监控params变化', val)
        if(val && val.topicList &&  val.topicList.length>0) {
            this.topicList = this.topicList.map((item: any) => {
                const topicInfo = val.topicList.find((_item: any)  => _item.id === item.id)
                if (topicInfo) {
                    item  =  Object.assign(item, topicInfo);
                }
                return  item;
            })
            // this.studentResult = val.topicList[this.activeIndex].studentResult
            console.log('---> this.topicList', this.topicList)
        }
    }

    destroyed() {
        console.log('destroyed -->');
        this.showSingleRanking = false;
        this.showRanking = false;

        this.timeDownTime && clearInterval(this.timeDownTime)

        this.$root.$off('event_countDown');
        this.$root.$off('event_studentResultSingle');
        this.$root.$off('event_questionMsg');
        this.$root.$off('event_tabChange');
        this.$root.$off('event_showRanking');

        // this.$root.$off('event_studentResultTotal');
    }
}
