


























import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Imagebox from '@/components/imagebox.vue';

@Component({
    components: {
        Imagebox
    }
})
export default class App extends Vue {
    @Prop(Array) readonly options: any[];
    @Prop(Array) readonly rightAnswerIndex: any[];
    @Prop(Array) readonly selectIndex: any[];
    @Prop(Array) readonly receiveSelectIndex: any[];
    @Prop(Boolean) readonly isTimeOver: boolean;

    private errorAnswerIndex: number = -1;
    private correctAnswerIndex: number = -1;

    get answerIndex() {
        return this.options.find(item => item.answer === 1).seq_no;
    }

    handleOption(index: number) {
        if (this.receiveSelectIndex.length > 0) return;
        // // 避免重复答题
        // if(this.errorAnswerIndex !== -1 || this.correctAnswerIndex !== -1) {
        //     return;
        // }
        this.$emit('handleOption', index);
    }

    mounted() {
    }

    @Watch('options')
    onTopicInfoChanged() {
        this.errorAnswerIndex = -1;
        this.correctAnswerIndex = -1;
    }
}
