


















import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'Imagebox',
})
export default class Imagebox extends Vue {
  @Prop(String) private readonly imageUrl: string;
  @Prop(String) private readonly type: string;
  public $refs: {
    bigWrap: any
  }
  private bigImgVisible: boolean = false;
  
  private handleBigimgIcon() {
    if (this.type === 'nobig') return;
    this.bigImgVisible = true;
  }
  private handleCloseIcon() {
    this.bigImgVisible = false;
  }
}
