





















































































import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import optionsText from "./optionsText.vue";
import optionsImage from "./optionsImage.vue";
import optionsJudge from './optionsJudge.vue';
import singleRanking from '@/components/topic/ranking/singleRanking.vue';
import topicRanking from '@/components/topic/ranking/index.vue';
import defaultBj from '@/components/topic/student/defaultBj.vue';
import Imagebox from '@/components/imagebox.vue';

import timeDown from "./timeDown.vue";
import { topicTypeSwichText } from '@/mock/mockJson'

const topic_type = 7002;

@Component({
  components: {
    optionsText,
		optionsImage,
		optionsJudge,
    timeDown,
    singleRanking,
    topicRanking,
    defaultBj,
    Imagebox,
  },
})
export default class App extends Vue {
  @Prop(Array) readonly rankingData: any;
  @Prop(Object) readonly params: any;
  @Prop(Object) readonly topicInfo: any;
  @Prop(Array) readonly answerMsgList: any;

  get optionType() {
    return this.topicInfo.options && this.topicInfo.options[0].type;
  }
  get type() {
    return this.topicInfo.type;
  }
  get rightAnswerIndex() {
    return this.topicInfo.rightAnswerIndex;
  }
  get currentOptions() {
		if (this.type === 5) {
			return "optionsJudge"
		} else {
			return	this.optionType === "text" ? "optionsText" : "optionsImage";
		}
  }

  get title_ext_type() {
    if (this.topicInfo.title_ext && this.topicInfo.title_ext.length) {
      return this.topicInfo.title_ext[0].type;
    } else {
      return "text";
    }
  }
	private topicTypeSwichText: {[propName: string]: string} = topicTypeSwichText;
  private hasAsk: boolean = false; // 是否已经答题
  private isTimeOver: boolean = false; // 是否倒计时结束
  private restTime: number = this.topicInfo.timeDown; // 倒计时剩余时间
  private timeDownTime: any = 0;
  private selectIndex: number[] = [];
  private receiveSelectIndex: number[] = [];
  private errorMp3Path: string = 'resource/template/audio/Match_error.mp3';
  private rightMp3Path: string = 'resource/template/audio/Match_right.mp3';
  private UUSoundComRight: any;
  private UUSoundComError: any;

  private showDefaultBj: boolean = true;
  private showRanking: boolean = false;
  private showSingleRanking: boolean = false;
  private rightUsersData: any[] = [];
  private rightUserTime: any;

  showLottie(type: string, cb? : ()=> void) {
    // @ts-ignore
    window.Lottie && Lottie.show(type, cb)
    this.soundReslutMp3(this.UUSoundComRight);
  }
  soundReslutMp3(com: any) {
    setTimeout(() => {
       com && com.play()
    }, 500)
  }
  playAudio() {
    const audio = document.getElementById("audio") as HTMLAudioElement;
    if (audio) {
      audio.play();
    }
  }
  handleOption(index: number) {
    if (this.type === 2) {
      if (this.selectIndex.includes(index)) {
        this.selectIndex = this.selectIndex.filter(
          (item: number) => item != index
        );
      } else {
        this.selectIndex.push(index);
      }
    } else {
      if (this.selectIndex.includes(index)) {
        this.selectIndex = [];
      } else {
        this.selectIndex = [index];
      }
    }
    console.log(">>> this.selectIndex", this.selectIndex);
  }
  handleSubmit() {
    this.sendResultMsg(this.selectIndex);
  }
  sendResultMsg(resultIndex: number[]) {
    console.log(">>> resultIndex", resultIndex);
    if (this.hasAsk) {
      return;
    }
    if (
      this.rightAnswerIndex.length === resultIndex.length
      &&
      this.rightAnswerIndex.every((item: number) => resultIndex.includes(item))
    ) {
      // 回答正确直接显示lottie(放到发送指令函数执行 防止刷新时候连续执行动画)
      this.showLottie(topic_type + '')
    } else {
      this.soundReslutMp3(this.UUSoundComError);
    }
    //@ts-ignore
    const topicModule = window.previewData.list[
      this.params.pageIndex - 1
    ].elements.find((item: any) => item.type === topic_type);
    if (!topicModule) return;
    const elementId = topicModule.id;
    this.hasAsk = true;
    const msg = {
      moduleType: 1,
      cmd: 7012,
      type: 6,
      id: elementId,
      msg: {
        id: this.topicInfo.id,
        option: resultIndex,
      },
    };
    // @ts-ignore
    window.PageMgr.sendMsg(JSON.stringify(msg));

    const saveJson = {
      page: this.params.pageIndex,
      topicId: this.topicInfo.id,
      answer: resultIndex,
      timeDown: this.topicInfo.timeDown,
      timeAnswer: Number(this.topicInfo.timeDown - this.restTime),
      rightAnswer: this.topicInfo.rightAnswerIndex,
      comType: topic_type,
    };
    // @ts-ignore
    window.ucPlugin && window.ucPlugin.save(saveJson);
  }

  timeOver(showSingleRanking: boolean) {
    console.log("time over");
    this.isTimeOver = true;
    this.sendResultMsg([-1]);
    if (!showSingleRanking) return;
    this.rightUserTime && clearTimeout(this.rightUserTime);
    const topicInfo = this.params.topicList.find((item: any) => {
      return (item.id === this.topicInfo.id)
    })
    if(topicInfo) {
      if(topicInfo.type === 1) {
        const studentResult = topicInfo.studentResult.find((item: any) => item.option === topicInfo.rightAnswerIndex[0])
        studentResult && (
          this.rightUsersData = studentResult.users
        )
      } else {
        const studentResult = topicInfo.studentResult.find((item: any) => item.text === '正确')
        studentResult && (
          this.rightUsersData = studentResult.users
        )
      }
			if (!this.rightUsersData || this.rightUsersData.length === 0) return;
      this.showSingleRanking = true;
      this.rightUserTime = setTimeout(() => {
        this.showSingleRanking = false;
      }, 3000);
    }
  }

  mounted() {
    
    setTimeout(() => {
    // @ts-ignore
      this.UUSoundComError = new UUSoundCom(this.errorMp3Path)
    // @ts-ignore
      this.UUSoundComRight = new UUSoundCom(this.rightMp3Path)
      // this.soundReslutMp3(this.UUSoundComRight);
    },500)


    console.log(">>> this.topicInfo", this.topicInfo);
    this.$root.$on("event_questionRes", (msgjson: any) => {
      const { msg, user } = msgjson;
      console.log("event_questionRes emit -->");
      console.log("---> msg , this.topicInfo", msg, this.topicInfo);
      // 判断接到的答案指令和当前题目是否为同一题
      // @ts-ignore
      console.log(">>> window.GlobalData.user.id", window.GlobalData.user.id);
      // @ts-ignore
      if (msg.id !== this.topicInfo.id || user.id !== window.GlobalData.user.id)
        return console.log(">>>> 题目id 或者 用户id 不一致");
      // const topicItem = this.params.topicList.find
      this.hasAsk = true;
      this.receiveSelectIndex = msg.option;
    });
    this.$root.$on("event_sendQuestion", (msgjson: any) => {
      this.showDefaultBj = false;
      this.hasAsk = false;
      this.selectIndex = [];
      this.receiveSelectIndex = [];
      this.isTimeOver = false;
    });
    // 习题讲解
    this.$root.$on("event_sendSetOn", (msgjson: any) => {
      const answerMsgList = this.answerMsgList.find((item: any) =>
        // @ts-ignore
        item.id === this.topicInfo.id && item.userId === window.GlobalData.user.id
      )
      this.showDefaultBj = false;
      this.hasAsk = true;
      this.selectIndex = answerMsgList ? answerMsgList.option : [];
      this.receiveSelectIndex = answerMsgList ? answerMsgList.option : [];
      this.isTimeOver = true;
    });
    this.$root.$on('event_showRanking', () => { 
        this.showRanking = true;
        setTimeout(() => {
            this.showRanking = false;
        }, 5000);
    })

    this.$root.$on("event_countDown", (receivemsg: any) => {
      const cb = (timeStamp: any) => {
        const currentTime = timeStamp;
        const { msg, time } = receivemsg;
        const { id, askTime } = msg;
        const countDown = parseInt(Number(currentTime - time) / 1000 + "");
        if (countDown >= askTime) {
          this.restTime = 0;
          // 处理刷新题目已经结束时不发送命令
          this.isTimeOver = true;
          this.hasAsk = true;
          // this.timeOver(false);
        } else {
          this.restTime = askTime - countDown;
          this.timeDownTime && clearInterval(this.timeDownTime);
          this.timeDownTime = setInterval(() => {
            if (this.restTime <= 0) {
              console.log(">>>  倒计时 student");
              this.restTime = 0;
              this.timeOver(true);
              clearInterval(this.timeDownTime);
            } else {
              // this.restTime--;
              this.restTime = Number(this.restTime - 1);
              // console.log('>>>> this.restTime--', this.restTime)
            }
          }, 1000);
        }
      };
      // @ts-ignore
      window.ucPlugin &&  window.ucPlugin.getServerTime().then((res: any) => {
					console.log(">>> 获取服务器当前时间", res);
					const timeStamp = new Date(res.time).valueOf();
					cb(timeStamp);
      });
    });

    this.$root.$emit("event_studentPageIsLoad");
  }

  destroyed() {
    console.log("destroyed -->");
    this.showSingleRanking = false;
    this.showRanking = false;
    this.isTimeOver = false;

    this.$root.$off("event_countDown");
    this.$root.$off("event_showRanking");

    this.$root.$off('event_sendQuestion');
    this.$root.$off('event_sendSetOn');
    this.timeDownTime && clearInterval(this.timeDownTime);
    this.rightUserTime && clearTimeout(this.rightUserTime);
  }
}
