


















































































const topic_type = 7002;

import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({})
export default  class Ranking extends Vue  {
  @Prop(Array) private readonly rankingData: any;
  @Prop(Object) private readonly params: any;
  private closeDisplay:boolean = window.GlobalData.userType === 2;
  private rankingMp3Path: string = 'resource/template/audio/Topic_Ranking.mp3';
  private UUSoundComRanking: any;
  
  showLottie(type: string, cb? : ()=> void) {
    // @ts-ignore
    window.Lottie && Lottie.show(type, cb);
    this.soundReslutMp3(this.UUSoundComRanking);
  }
  soundReslutMp3(mp3Com: any) {
    setTimeout(() => {
      mp3Com && mp3Com.play()
    }, 500)
  }
  mounted() {
    // 注册音频
    //  播放动画
    setTimeout(() => {
      // @ts-ignore
      this.UUSoundComRanking = new UUSoundCom(this.rankingMp3Path)
      this.showLottie('ranking')
    }, 500)
  }
  close() {
    // @ts-ignore
    const topicModule = window.previewData.list[this.params.pageIndex - 1].elements.find((item: any) => item.type === topic_type);
    if(!topicModule) {
      return;
    }
    const elementId = topicModule.id;
    const msg = {
        id: elementId,
        moduleType: 1,
        cmd: 7002,
        type: 2,
    };
    // @ts-ignore
    window.PageMgr.sendMsg(JSON.stringify(msg));
  }
}
