













import { Component, Vue, Watch } from 'vue-property-decorator';
import topicStudent from '@/components/topic/student/index.vue';
import topicTeacher from '@/components/topic/teacher/index.vue';
import topicRanking from '@/components/topic/ranking/index.vue';
import defaultBj from '@/components/topic/student/defaultBj.vue';

// import singleRanking from '@/components/topic/ranking/singleRanking.vue';


import studentResultType1 from '@/mock/studentResultType1';
import studentResultType2 from '@/mock/studentResultType2';

// 测试数据
import { params, topicInfo, studentResultType3 } from '@/mock/mockJson';

const topic_type = 7002;
const initParams = {
  topicList: [],
  pageIndex: -1,
  activeId: null,
}

@Component({
    components: {
      topicStudent,
      topicTeacher,
      topicRanking,
      defaultBj,
    }
})
export default class App extends Vue {
  private currentComponent: string = 'topicTeacher'; 
  private topicDisplay: boolean = false;
  private rankingData: any = [];
  private params: any = {
    topicList: [],
    pageIndex: -1,
    activeId: null,
  };
  private answerMsgList: any[] = [];
  private topicInfo: any = {};
  private eventList: any = []; // 缓存指令列表
  private showSendRanking: boolean = false;
  private labelSwitchIndex: any = {
    'A': 0,
    'B': 1,
    'C': 2,
    'D': 3,
    'E': 4,
    'F': 5,
    'G': 6,
  }
  // @ts-ignore
  private rolePageIsLoaded: boolean = false; // 老师/学生页面mounted事件标记

  emit(eventName: string, message: any) {
    if(this.rolePageIsLoaded) {
      this.$root.$emit(eventName, message);    
    } else {
      this.eventList.push([eventName, message]);
    }
  }

  emitAll() {
    if(this.rolePageIsLoaded) {
      this.eventList.forEach((item: any) => {
        this.$root.$emit(item[0], item[1]);    
      })
    }
  }
  // created() {
  //   this.topicDisplay = true;
  //   this.params = params;
  //   this.topicInfo = topicInfo;
  //   this.currentComponent = 'topicTeacher';
  // }
  mounted() {
     
    // console.log('-------------------> App mounted');
    console.log('App mounted -->');
    // 监听学生端页面的mounted钩子 -- 用于处理恢复指令时的情况
    this.$root.$on('event_studentPageIsLoad', () => {
      console.log('event_studentPageIsLoad emit --->');
      if(!this.rolePageIsLoaded) {
        this.rolePageIsLoaded = true;
        this.emitAll();
      }
    })
    // 监听老师端页面的mounted钩子 -- 用于处理恢复指令时的情况
    this.$root.$on('event_teacherPageIsLoad', () => {
      console.log('event_teacherPageIsLoad emit --->');
      if(!this.rolePageIsLoaded) {
        this.rolePageIsLoaded = true;
        this.emitAll();
      }
    })

    // @ts-ignore
    let observer = Observer.getInstance();
    observer.regist('topic', (msg: string) => {
      console.log('vue topic emit -->');
      console.log(msg);
      // 角色设置，1，学生，2老师
      const userType = window.GlobalData.userType;
      console.log('userType = ' + userType);
      // @ts-ignore
      const previewData = window.previewData;
      const msgJson = JSON.parse(msg);
      const { pageIndex, cmd, timeOver} = msgJson;
      let message = msgJson.msg;
      // let topicId: any, option;
      // if(message) {
      //   topicId = message.id;
      //   option = message.option;
      // }
      const topicModule = previewData.list[pageIndex - 1].elements.find((item: any) => item.type === topic_type);
      this.initStudentResult(pageIndex);
      if(cmd === 7001) {
        if(!topicModule) {
          return;
        }
        this.topicDisplay = true;
        // 老师端 展示
        if(userType === 2) {
          this.currentComponent = 'topicTeacher';
        } else {
          this.currentComponent = 'defaultBj';
        }
        // 处理重新回到当前页时保留答题数据
        this.isQeustionEnd()
      } else if(cmd === 7002) {
        // 隐藏
        this.topicDisplay = false;
      } else if(cmd === 7011) {
        // 老师端 发送问题
        this.params.activeId = message.id;
        this.changeTopicInfo()
        if (timeOver && userType === 1) {
          // 指令7011 如果timeOver为true说明发送的是习题讲解指令
          this.topicDisplay = true;
          this.currentComponent = 'topicStudent';
          this.emit('event_sendSetOn', message);
          return;
        }
        if(!timeOver && userType === 2) {
          this.emit('event_questionMsg', msgJson);
          return;      
        }
        if(!timeOver && userType === 1) {
          // console.log('老师端 发送问题 -- 学生端');
          this.currentComponent = 'topicStudent';
          this.emit('event_sendQuestion', message);
          return;      
        } 
        // console.log('topicInfo');
        // console.log(this.topicInfo);
      } else if(cmd === 7012) {
        // 老师接收的学生答题情况指令（单个学生）
        console.log('>>>> cmd 7012')
        console.log('>>>  调用pushStudentReslut this.params.topicList', this.params.topicList)
        this.pushStudentReslut(msgJson, this.params.topicList);
        if(userType === 2) {
          this.emit('event_studentResultSingle', msgJson);
        } else {
          // 处理刷新推送多指令
          this.emit('event_questionRes', msgJson);
        }
      } else if(cmd === 7031) {
        // 倒计时指令
        this.emit('event_countDown', msgJson);
      }else if(cmd === 7044) {
        //  当前选中的哪一题
        console.log('>>>> cmd 7044  cmd')
        const { id, index } = msgJson.msg;
        this.emit('event_tabChange', msgJson);
      }else if (cmd === 7055)  {
        // 倒计时结束
        console.log('>>>> cmd 7055  cmd')
        const { id } = msgJson.msg;
        this.params.topicList = this.params.topicList.map((item: any, index: number) => {
          if(item.id === id) {
            item.timeOver = true;
          }
          return item;
        });
        this.changeTopicInfo()
        this.isQeustionEnd()
      } else if (cmd === 7066) {
        // 发送排名
        console.log(">>>>  cmd 7066  cmd")
        // 处理排名
        this.countRanking()
        setTimeout(() => {
          this.emit('event_showRanking', msgJson);
        }, 0)
        console.log('>>>> rankingData',  this.rankingData);
      }
    })
    observer.regist('topicShow', (pageIndex: number) => {
      this.initStudentResult(pageIndex);
      this.topicDisplay = true;
      // 角色设置，1，学生，2老师
      const userType = window.GlobalData.userType;
      console.log('userType = ' + userType);
      // 老师端 展示
      if(userType === 2) {
        this.currentComponent = 'topicTeacher';
      } else {
        this.currentComponent = 'defaultBj';
      }
      // 处理重新回到当前页时保留答题数据
      this.isQeustionEnd()
    })
    observer.regist('topicHidden', () => {
      this.topicDisplay = false;
      this.params = initParams;
      this.rankingData = [];
      this.topicInfo = {};
      this.eventList = [];
      this.showSendRanking =  false;
      this.rolePageIsLoaded = false;
      this.answerMsgList =[];
    })
  }
  initStudentResult(pageIndex: number) {
    let topicList =  null;
    // @ts-ignore;
    const topicModule = window.previewData.list[pageIndex - 1].elements.find((item: any) => item.type === topic_type);
    if(topicModule) {
      // 初始化 topicList studentResult;
      topicList = topicModule.props.questionList;
      topicList = topicList.map((item: any) => {
        if(!item.studentResult) {
          // 单选题 学生结果初始化
          if(item.type === 1) {
            item.studentResult = item.options.map((Oitem: any, index: number) => {
              return {
                option: Oitem.seq_no,
                count: 0,
                users: [],
              }
            })
          } else {
            // 判断题和多选题 学生结果初始化
            item.studentResult = JSON.parse(JSON.stringify(studentResultType3));
          }
        }
        return item;
      })
      this.params = {
        topicList: topicList,
        pageIndex,
        activeId: this.params.activeId?this.params.activeId: topicList[0].id,
      }
    }
  }
  // 计算排名
  countRanking() {
    const rankingDataObj: any = {};
    const allQuestionMsg: any = []
    this.params.topicList.forEach((item: any) => {
      item.studentResult.forEach((rItem: any) => {
        rItem.users.forEach((uItem: any) => {
          let questionObj = {
            ...uItem,
            userId: uItem.id,
            id: item.id,
            isRight: false,
            rightAnswer: item.rightAnswerIndex,
          }
          if (item.type === 1) {
            questionObj.isRight = rItem.option === item.rightAnswerIndex[0];
          } else {
            questionObj.isRight = rItem.text === '正确';
          }
          allQuestionMsg.push(questionObj)
        })
      })
    });
    allQuestionMsg.forEach((item: any) => {
        if(!rankingDataObj[item.userId])  {
          item.rightCount = 0;
          rankingDataObj[item.userId] = item
        } 
        if(item.isRight)  {
          rankingDataObj[item.userId].rightCount++
        }
    })
    let rankingData = Object.values(rankingDataObj).sort((a: any, b: any) => {
      return   b.rightCount - a.rightCount;
    })
    const count = this.params.topicList.length;
    rankingData = rankingData.map((item: any) => {
      item.correct = ((item.rightCount/count)*100).toFixed(2);
      return item;
    })
    this.rankingData = rankingData
  }
  pushStudentReslut(msgjson: any, topicList: any) {
    // console.log('>>>> pushStudentReslut  接收 topicList', topicList)
    const { user, msg, pageIndex } = msgjson
    const { id, option } = msg;
    this.answerMsgList.push({
      id,
      option,
      userId: user.id,
    })
    topicList = topicList.map((item: any) => {
      if (!user.id || user.id === '') {
        console.warn(">>>>> 用户id为空", msgjson)
        return item;
      }
      if(id && id === item.id) {
          item.studentResult = item.studentResult.map((oitem: any) => {
            const userInfo =  oitem.users.find((uItem: any) => uItem.id === user.id)
            if (item.type === 1) { 
              if(oitem.option === option[0]) {
                if(!userInfo) {
                  oitem.users.push(user)
                  oitem.count++
                }
              }
            } else {
              if(
                item.rightAnswerIndex.length === option.length
                &&
                item.rightAnswerIndex.every((Aitem: number) => option.includes(Aitem))
                &&
                oitem.text  === '正确'
              ) {
                if(!userInfo) {
                  oitem.users.push(user)
                  oitem.count++
                }
              }  
              if (
                oitem.text !== '正确'
                && 
                (item.rightAnswerIndex.length !== option.length
                  ||
                !item.rightAnswerIndex.every((Aitem: number) => option.includes(Aitem)))
              ) {
                if(!userInfo) {
                  oitem.users.push(user)
                  oitem.count++
                }
              }
            }
            return oitem;
          })
        
      }
      return item
    })
    this.params.topicList = topicList;
    //@ts-ignore
    const topicModule = previewData.list[pageIndex - 1].elements.find((item: any) => item.type === topic_type);
    if(topicModule) {
      topicModule.props.questionList = topicList;
    }
    console.log(">>>> this.params.topicList", this.params.topicList)
  }
  changeTopicInfo() {
    const {topicList, activeId} = this.params
    this.topicInfo = topicList.find((item: any) => item.id === activeId);
  }
  isQeustionEnd() {
    const topicItem = this.params.topicList.find((item: any) => !item.timeOver);
    console.log('>>>>> isQeustionEnd', this.params.topicList)
    if(!topicItem) {
      this.showSendRanking = true;
    }
  }
  
  destroyed() {
    console.log('-------------------> App destroyed');
  }
}
