import Vue from 'vue';

Vue.filter('index-filter', (index: number) => {
    let charcode;
    const indexFilter = index - 1;
    return indexFilter.toString(26).split('').map((item, i) => {
        charcode = item.charCodeAt(0);
        charcode += (charcode >= 97 ? 10 : 49);
        return String.fromCharCode(charcode);
    }).join('').toUpperCase();
});
