



























import { Component, Vue, Prop } from 'vue-property-decorator';
import optionsText from './optionsText.vue';
import optionsImage from './optionsImage.vue';
import optionsJudge from './optionsJudge.vue';
import { topicTypeSwichText } from '@/mock/mockJson';
import Imagebox from '@/components/imagebox.vue';

@Component({
    components: {
        optionsText,
        optionsImage,
        optionsJudge,
        Imagebox,
    }
})
export default class App extends Vue {
    @Prop(Object) readonly topicInfo: any;

    get optionType() {
        return this.topicInfo.options && this.topicInfo.options[0].type;
    }
    get type() {
        return this.topicInfo.type;
    }
    
    get currentOptions() {
        if (this.type === 5) {
			return "optionsJudge"
		} else {
			return	this.optionType === "text" ? "optionsText" : "optionsImage";
		}
    }
	private topicTypeSwichText: {[propName: string]: string} = topicTypeSwichText;

    private receiveSelectIndex: number[] = [];

    playAudio() {
        const audio = document.getElementById('audio') as HTMLAudioElement;
        if(audio) {
            audio.play();
        }
    }

    mounted() {
        console.log('this.topicInfo --->');
        console.log(this.topicInfo);
    }
}
