
























const topic_type = 7002;
import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({})
export default class Ranking extends Vue {
  @Prop({    type: Array, default: () => [] }) private readonly rightUsersData: any;

  private list: Array<any> = [];
  private width: number = 0;
  private startWidth: number = 0;
  private left: number = 0;
  private rankingMp3Path: string = 'resource/template/audio/Topic_Single_Ranking.mp3';
  private UUSoundComRanking: any;
  soundReslutMp3(mp3Com: any) {
    setTimeout(() => {
      mp3Com && mp3Com.play()
    }, 500)
  }
  // 创建指定数量的星星
  private createStart() {
    this.list.forEach((item: any, index: number) => {
      //@ts-ignore
      const dom = this.$refs['start' + index][0];
      const toOffset = dom.getBoundingClientRect();
      console.log("Before", toOffset.top, JSON.stringify(toOffset));
      dom.style = `position: fixed;left:${this.left}px ; top: 0;`;
      const formOffset = dom.getBoundingClientRect();
      console.log("After", formOffset.top, JSON.stringify(formOffset), dom.clientLeft, dom.scrollLeft);
      setTimeout(this.moveStart.bind(this, dom, formOffset, toOffset), 100);
    });
  }

  // 移动星星
  private moveStart(dom: any, form: any, to: any) {
    console.warn(form.left, form.top, to.left - (this.width/2), to.top);
    dom.style = `position: fixed;left:${this.left}px ;top: 0px; transform: translate(${to.left - (this.width/2) + (this.startWidth/2)}px, ${to.top}px)`; 
  }

  mounted() {
    console.log(this.$el.clientWidth, this.$el.clientHeight);
    this.list = JSON.parse(JSON.stringify(this.rightUsersData));
    this.$nextTick(()=>{
      if (this.list.length > 0) {
        this.width = document.body.clientWidth;
        console.log('>>> dom', this.width)

        //@ts-ignore
        const dom = this.$refs['start' + 0][0];
        this.startWidth = dom.width;
        const left = Number(this.width/2 - this.startWidth/2) 
        this.left = left
        console.log('>>> dom', this.width, dom.width)
        this.createStart()
        // this.startWidth
        // @ts-ignore
        this.UUSoundComRanking = new UUSoundCom(this.rankingMp3Path)
        setTimeout(() => {
          this.soundReslutMp3(this.UUSoundComRanking);
        }, 500)
      }
    })
    
  }
}
